import styled from "styled-components";

const StyledLikesAndComments = styled.p`
    font-size: 12px;
    font-weight: 700;
    color: #bebebe;
`;

function LikesAndComments({likes, comments}) {
    return(
        <StyledLikesAndComments> {likes}   {comments}</StyledLikesAndComments>
    );
}

export default LikesAndComments;