export default function CalculateTimeDifference(timestamp, type) {
    const currentTime = new Date();
    const targetTime = new Date(timestamp);
    const timeDiff = currentTime.getTime() - targetTime.getTime();
    // type 1:   ,
    if (type) {
        if (timeDiff < 60000) {
            return " ";
        } else if (timeDiff < 3600000) {
            const minutesDiff = Math.floor(timeDiff / 60000);
            return `${minutesDiff} `;
        } else if (timeDiff < 86400000) {
            const hoursDiff = Math.floor(timeDiff / 3600000);
            return `${hoursDiff} `;
        } else {
            const daysDiff = Math.floor(timeDiff / 86400000);
            return `${daysDiff} `;
        }
    }
    // type 0 :   ,
    else {
        const daysDiff = Math.floor(timeDiff / 86400000);
        return `${daysDiff}`;
    }
}
  